* {
    box-sizing: border-box;
}

body {
    overflow: hidden;
    margin: 0;
    background-image: url("https://media4.giphy.com/media/iE3KWRmMHlpTT6B45K/giphy.gif");
    height: 100vh;
    justify-content: center;
    align-items: center;
}

.container {
    width: 100%;
    display: flex;
    padding: 0 20px;
}


.slide {
    height: 80vh;
    border-radius: 20px;
    margin: 10px;
    cursor: pointer;
    color: #fff;
    flex: 1;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;
    transition: all 500ms ease-in-out;
}

.slide h3 {
    position: absolute;
    font-size: 24px;
    bottom: 20px;
    left: 20px;
    margin: 0;
    opacity: 0;
}

.slide.active {
    flex: 10;
}

.slide.active h3 {
    flex: 80px;
    opacity: 1;
    transition: opacity 0.3s ease-in;
}